:root {
	--text-color: black;
	--title-color: black;
	--background-color: rgb(218, 218, 218);
}

.navbar-brand
{
  font-family: 'Lato', sans-serif;
  color:grey;
  font-size: 32px !important;
  margin: 0px;
}

.robot-objects{
	padding: 0px !important;

}

.robot-card{
	padding: 12px 0 !important;
	margin: 12px 10% !important;


}

.robot-search{
	padding-bottom: 18px !important;
}


.robot-object{
	display: grid;
	gap: 10px;
	grid-template-columns: repeat(auto-fit, minmax(200px, 1.5fr));
	grid-auto-flow: dense;
	// padding: 12px;	
	background-color: white;
	border-radius: 8px;
	color: #757575;
	// border: 1px solid #616161;
	background-color: #ffffff;
	opacity: 0.7;
	float: inline-start;
	width: auto;
	// margin: 15px 0;	
}

.animation-button{
	background-color: white;
	border-radius: 12px;
	color: black;
	border: 1px solid #64a8db;
	background-color: #ffffff;
	opacity: 0.95;
	float:inline-end;
	width:auto;		
	margin-left: 5px;
	padding: 5px;
}
.animation-button:hover{
	border: 1px solid #72db64;
}

.remove{
	color: red;
	text-align: right;
}

body {

	font-family: 'Open Sans', sans-serif;
	// padding-top: 100px;
	// padding: 50px;
	background-color: var(--background-color);
	background-color: #cfd9e4;		


	
	&.background-night {
		background-color: #b4b0b0;
	}

	&.background-night:disabled{
		border: 1px solid #999999;
		background-color: #cccccc;
		color: #666666;
	}


	main {
		max-width: 1000px;
		margin: 0 auto;



		.top-font-sizes {
			color: rgb(0, 0, 0);
			padding-inline-start: 746px;
		}

	}

	h1 {
		font-weight: 300;
	}

	
	}

	header {
		width: 100%;
		flex-shrink: 0;
		border-radius: 6px;
		background-color: #fff;
		box-shadow: 0 2px 6px 1px #e1e5e8;
		margin-bottom: 14px;
		display: flex;
		flex-direction: column;
		border: 1px solid #fff;
		padding: 10px 20px;
		position: relative;

		input {
			width: 100%;
			text-overflow: ellipsis;
			display: inline-block;
			color: var(--text-color);
			font-size: 14px;
			outline: 0;
			transition: border .1s ease;
			resize: none;
			border: none;

			&::placeholder  {
				color: #b6c1cd;
			}
		}
	}

	a {
		color: var(--text-color);
		cursor: pointer;
		font-weight: 500;
		font-size: 12px;
	}

			footer {

				display: flex;
				margin-bottom: 0px;
				justify-content: space-between;


				.meta-data {
					color: var(--text-color);
					font-size: 12px;
					margin: 10px 0;
					flex-shrink: 0;
				}
			}
		
	

